@import url('https://fonts.googleapis.com/css2?family=Do+Hyeon&display=swap');
* {
  margin: 0;
  padding:0;
  box-sizing: border-box;
}
body {
  font-family: 'Do Hyeon', sans-serif;
  /* background:rgb(0, 128, 255); */
  background: rgb(0, 110, 255);
}
.container {
  width: 100%;
  max-width: 600px;
  background: #fff;
  margin: auto;
  margin-top: 150px;
  padding: 40px;
  border-radius: 8px;
  box-shadow: 8px 8px 16px rgba(0,0,0,0.15);
}
.title{
  font-size: 32px;
  line-height: 32px;
  font-weight: 800;
}
.quote {
  margin-top: 20px;
  font-size: 20px;
  text-justify: auto;
}
.footer {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 20px;
  /* border: 2px solid red; */
}
.refbtn {
  height: 100%;
}
.rebtn {
  padding: 12px 32px;
  border-radius: 6px;
  border: 0;
  background: rgb(0, 123, 246);
  color: #fff;
  text-transform: uppercase;
  font-weight: 600;
  letter-spacing: 1px;
  box-shadow: 0 6px rgba(0,0,0,0.15);
  position: relative;
  top: 0;
  left: 0;
  transition: 0.15s;
}
.rebtn:hover {
  top: 3px;
  left: 0;
  box-shadow: 0 3px rgba(0,0,0,0.15);
}
.rebtn:active {
  top: 6px;
  left: 0;
  box-shadow: 0 0 rgba(0,0,0,0.15);
}
.social {
  display: inline-block;
  width: 32px;
  height: 32px;
  line-height: 36px;
  text-align: center;
  font-size: 20px;
  margin-left: 8px;
  color: #fff;
  /* border: 2px solid red; */
}
.facebook {
  background: #1877F2;
}
.twitter {
  background: #1DA1F2;
}
@media screen and (max-width: 768px) {
  #root {
    padding: 20px;
  }
}
@media screen and (max-width: 480px) {
  .container {
    padding: 32px 20px;
    margin: auto;
  }
}